import React from "react";
import {Box, Button, CardMedia, Typography} from "@mui/material";
import GradientContainer from "../theme/GradientContainer";

const Home: React.FC = () => {
    const handleSignup = async (email: string) => {
        try {
            const response = await fetch(`/mail/emaillist`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({
                    email: `${email}`, // Example payload, customize as needed
                }),
            });
            const data = await response.json();
            if (response.ok) {
                console.log('Signup successful:', data);
            } else {
                console.error('Signup failed:', data);
            }
        } catch (error) {
            console.error('Error during API call:', error);
        }
    };

    return (
        <>
            <GradientContainer>
                <Box
                    sx={{
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        minHeight: "100vh",
                        gap: "4rem",
                        padding: {xs: "5rem", md: "15rem"},
                        flexWrap: "wrap", // Ensures proper layout on smaller screens
                        textAlign: "center",
                    }}
                >
                    {/* Text Section */}
                    <Box
                        sx={{
                            flex: "1 1 auto",
                            maxWidth: "600px", // Constrains text width for better readability
                            padding: {xs: "1rem", sm: "2rem"},
                        }}
                    >
                        <Typography
                            variant="h1"
                            color="inherit"
                            sx={{
                                fontSize: {xs: "2rem", sm: "3rem", md: "4rem", lg: "5rem"},
                                lineHeight: "1.2",
                                marginBottom: "1rem",
                            }}
                        >
                            ProjectsIncluded
                        </Typography>
                        <Typography
                            variant="h4"
                            color="inherit"
                            sx={{
                                fontSize: {xs: "1.2rem", sm: "1.5rem", md: "2rem"},
                                fontStyle: "italic",
                                marginTop: "1rem",
                                wordBreak: "break-word",
                                maxWidth: "100%",
                            }}
                        >
                            "Elevate your applications with seamless scalability"
                        </Typography>
                    </Box>

                    {/* Image Section */}
                    <CardMedia
                        component="img"
                        image="cloud_server_secure.png"
                        alt="Secure Cloud Server"
                        sx={{
                            flex: "0 1 auto",
                            width: {xs: "80%", sm: "60%", md: "50%", lg: "40%"},
                            maxWidth: "400px", // Limits the max size of the image
                            height: "auto",
                        }}
                    />
                    <Button onClick={() => handleSignup("r.flinterman@projectsincluded.net")} variant="contained"
                            sx={{width: {xs: "80%", sm: "50%", md: "30%"}}}>
                        Signup for the Newsletter
                    </Button>
                </Box>
            </GradientContainer>
        </>
    );
};

export default Home;
